import React, {
  useState,
  ReactNode,
  useEffect,
  FC
} from 'react';

type sizes = 'xs' | 'sm' | 'md' | 'lg' | 'xl';

export interface ButtonProps {
  children: ReactNode;
  onClick?: () => void;
  size?: sizes;
  radius?: sizes;
  style?: React.CSSProperties;
}

const Button: FC<ButtonProps> = ({
  children,
  size = 'sm',
  radius = 'sm',
  style,
  onClick
}: ButtonProps) => {

  const [sizeExists, setSizeExists] = useState(false);

  useEffect(() => {
    size
      ? setSizeExists(true)
      : setSizeExists(false);
  }, [size]);

  const heightValue =
    size === 'xs'
      ? 30
      : size === 'sm'
        ? 40
        : size === 'md'
          ? 45
          : size === 'lg'
            ? 50
            : 55

  const radiusValue =
    size === 'xs'
      ? 0
      : size === 'sm'
        ? 15
        : size === 'md'
          ? 10
          : size === 'lg'
            ? 15
            : 20

  const defaultStyles = {
    border: '1px solid #ccc',
    borderRadius: radiusValue,
    padding: '5px 20px',
    textAlign: 'center',
    wrap: 'nowrap',
    width: 'auto',
    height: heightValue,
    ...style,
    } as React.CSSProperties;

  return (
    <button
      onClick={onClick}
      style={
        style
          ? style
          : defaultStyles
      }
    >
      {children}
    </button>
  )
}

export default Button;
